/* Butterfly.css */
.butterfly-container {
  position: relative;
  width: 100%;
  height: 10vh;
}

.butterfly {
  /* width: 50px; */
  /* height: 50px; */
  /* background-color: #ffc0cb; */
  /* background-image: url("images/butterfly1.jpg"); */
  /* border-radius: 50%; */
  position: absolute;
  left: -100px; /* Initial position outside the screen */
  animation: flyAcross 22s linear infinite;
}

@keyframes flyAcross {
  to {
    transform: translateX(calc(100% + 1450px)); /* Move across the screen */
  }
}
